import axios from "axios";
import React, { useEffect, useState } from "react";
import './GridMap.css'



const Tile: React.FC<{ type: string }> = (props) => {
    let stystr = '';
    switch (props.type) {
        case '#':
            stystr = '-200px -150px';
            break;
        case 'v':
            stystr = '-150px -50px';
            break;
        case '@':
            stystr = '-150px 0';
            break;
        case '<':
            stystr = '0 -150px';
            break;
        case '>':
            stystr = '-50px -150px';
            break;
        default:
            stystr = '0 0';
    }
    let tileStyle = {
        backgroundPosition: `${stystr}`,
        backgroundSize: '500% 400%',
    };
    return (
        <div className='grid-tile' style={tileStyle}>
        </div>
    );
}

interface PlayerProps {
    x: number;
    y: number;
    d: number;
    move: number;
}

const Player: React.FC<PlayerProps> = (props) => {
    let playerStyle = {
        marginLeft: `${props.x * 50 - 5}px`,
        top: `${props.y * 50 - 30}px`,
        backgroundPosition: `${-props.d * 60}px 0`,
        display: 'none',
    }
    let moveStyle = {
        marginLeft: `${props.x * 50 - 5}px`,
        top: `${props.y * 50 - 30}px`,
        backgroundPosition: `${-props.d / 4 * 60}px ${(props.move - 1) * 80}px`,
        display: 'none',
    }
    if (props.move !== 0)
        moveStyle.display = 'block';
    else
        playerStyle.display = 'block';

    return (
        <>
            <div className='bird' style={playerStyle} />
            <div className='move-bird' style={moveStyle} />
        </>
    );
}

interface TargetProps {
    x: number;
    y: number;
}

const Target: React.FC<TargetProps> = (props) => {
    let TargetStyle = {
        marginLeft: `${props.x * 50}px`,
        top: `${props.y * 50}px`,
    }

    return (
        <>
            <div className='pig' style={TargetStyle} />
        </>
    );
}

const GridMap: React.FC<{ map: string[] }> = (props) => {

    const [cmdcnt, setCmdcnt] = useState(0);
    const [cmd, setCmd] = useState("");
    const [map, setMap] = useState(props.map)
    let row = map.length, col = map[0].length
    let init_x = 0, init_y = 0, init_d = 0;
    let target_x = 0, target_y = 0;

    for (let i = 0; i < row; i++)
        for (let j = 0; j < col; j++) {
            if ("LRUD".includes(map[i][j])) {
                init_x = j;
                init_y = i;
                init_d = map[i][j] == 'U' ? 0 : 'R' ? 1 : 'D' ? 2 : 3;
            }
            if (map[i][j] == 'P') {
                target_x = j;
                target_y = i;
            }
        }

    const [sx, setSx] = useState(init_x);
    const [sy, setSy] = useState(init_y);
    const [dir, setDir] = useState({ d: init_d, i: init_d * 4, dt: 0 });
    const [dx, setDx] = useState(0);
    const [dy, setDy] = useState(0);
    const [win, setWin] = useState(0);



    let gmap: React.DetailedHTMLProps<React.HTMLAttributes<HTMLDivElement>, HTMLDivElement>[] = []


    const validPosition = (x: number, y: number) => {
        if (!(x >= 0 && x < col && y >= 0 && y < row)) return false;
        if (Math.abs(sx - x) < 0.2 && Math.abs(sy - y) < 0.2 && map[y][x] === 'P' && win === 0) {
            alert('');
            setWin(1);
        }
        return " LRUDP".includes(map[y][x]);
    }

    useEffect(() => {
        if (dx !== 0) {
            setTimeout(() => {
                if (dx < 0) {
                    if (validPosition(Math.ceil(sx) - 1, sy))
                        setSx(sx - 0.125);
                    setDx(dx + 1);
                } else {
                    if (validPosition(Math.floor(sx) + 1, sy))
                        setSx(sx + 0.125);
                    setDx(dx - 1);
                }
            }, 50);
        }
    }, [dx]);

    useEffect(() => {
        if (dy !== 0) {
            setTimeout(() => {
                if (dy < 0) {
                    if (validPosition(sx, Math.ceil(sy) - 1))
                        setSy(sy - 0.125);
                    setDy(dy + 1);
                } else {
                    if (validPosition(sx, Math.floor(sy) + 1))
                        setSy(sy + 0.125);
                    setDy(dy - 1);
                }
            }, 50);
        }
    }, [dy]);

    useEffect(() => {
        if (dir.d * 4 !== dir.i) {
            setTimeout(() => {
                setDir({ d: dir.d, i: (dir.i + dir.dt) % 16, dt: dir.dt })
            }, 40);
        }
    }, [dir]);

    useEffect(() => {
        if (cmdcnt < cmd.length) {
            setTimeout(() => {
                if (cmd[cmdcnt] === 'L')
                    turnLeft();
                if (cmd[cmdcnt] === 'R')
                    turnRight();
                if (cmd[cmdcnt] === 'F')
                    moveForward();
                setCmdcnt(cmdcnt + 1);
            }, 600);
        } else if (cmdcnt > 0) {
            setCmd("");
            setCmdcnt(0);
        }
    }, [cmd, cmdcnt]);

    const turnLeft = () => {
        setDir({ d: (dir.d + 3) % 4, i: dir.i, dt: 15 })
    }

    const turnRight = () => {
        setDir({ d: (dir.d + 1) % 4, i: dir.i, dt: 1 })
    }

    const moveForward = () => {
        if (dir.d === 0) {
            setDy(- 8);
        }
        else if (dir.d === 1) {
            setDx(8);
        }
        else if (dir.d === 2) {
            setDy(8);
        }
        else {
            setDx(- 8);
        }
    }
    const fetchCmd = () => {
        axios.get('http://localhost/code-game/cmd')
            .then(function (response) {
                // handle success
                setCmd(response.data);
                console.log(response);
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .then(function () {
                // always executed
            });
    }

    const fetchMap = () => {
        axios.get('http://localhost/code-game/map')
            .then(function (response) {
                // handle success
                setMap(response.data);
            })
            .catch(function (error) {
                // handle error
                console.log(error);
            })
            .then(function () {
                // always executed
            });
    }

    const updateMap = () => {
        gmap = [];
        for (let i = 0; i < row; i++) {
            let tiles = []
            for (let j = 0; j < col; j++) {
                tiles.push(<Tile key={j} type={map[i][j]} />);
            }
            gmap.push(<div key={i} className='grid-row'>{tiles}</div>);
        }
    }
    updateMap();

    return (
        <div id='gridMap'>
            {gmap}
            <Player x={sx} y={sy} d={dir.i} move={dx < 0 ? dx - 9 : dx > 0 ? 9 - dx : dy < 0 ? dy - 9 : dy > 0 ? 9 - dy : 0} />
            <Target x={target_x} y={target_y} />
            <button id='run' onClick={fetchCmd}>  </button>
        </div>
    );
}

export default GridMap;